import React, { Component } from 'react';
import Link from 'next/link';

class ProductList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			hover: false
		}
	}

	toggleHover() {
		this.setState({hover: !this.state.hover})
	}

	render() {
		const {product} = this.props;
		const rawJson = product.short_description;
		const productDescription = rawJson.replace(/(<([^>]+)>)/ig,"");
		const hoverImg = this.state.hover ? 'product.images[1].src' : 'product.images[0].src';
		return (
			<div>
				<Link
					href={`/product`}
					as={`/product/${product.slug}`}
				>
					<img
						src={`${hoverImg}`}
						alt="Product image"
					/>
				</Link>
				<div className="product-headings">
					<h3>{product.name}</h3>
					<h5>${product.price}</h5>
					<h5 className="product-description">{productDescription}</h5>
				</div>
				<style jsx>{`
					img {
						max-width: 450px;
					}
					.product-headings {
						text-align: center;
					}
					.product-headings {
						text-transform: uppercase;
					}
				`}</style>
			</div>
		);
	}
}

export default ProductList;